import React, {Component} from 'react';
import api from 'util/Api';
import LeftList from 'components/LeftList';
import EllipsisTooltip from 'components/EllipsisTooltip';

const columns = [
    {
        title: 'Cd Produto',
        dataIndex: 'cod',
        key: 'cod',
    },
    {
        title: 'Descrio',
        dataIndex:'description1',
        key: 'description1',
        
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 200
                }
            }
        },
        render: (text) => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    },

    {
        title: 'Grupo',
        dataIndex:'category.description',
        key: 'category',
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 150
                }
            }
        },
        render: (text) => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    }

]

const model = 'Product';


class ListProducts extends Component {
    constructor(props) {
        super();        
        console.log('ListUSers - Constructor')
        console.log(props)

        this.state = {
            data: []
        };
    }

    componentDidMount() {
        console.log('ComponentDidMout - CustomerList');
        this.fetchLeftList();
    } 

    fetchLeftList = () => {
        console.log('fecthLeftlist');
        api.get(`${model}/?sort=cod ASC&limit=999`, {})
        .then((result) => {
            console.log(result.data);
            this.setState({
                data: result.data
            });
        },)
        .catch(function(error) {
            console.log('error', error)
        })
    }


    render() {
        return (
            <LeftList 
                columns={columns}
                data={this.state.data}
                onRowClick={(record, rowIndex) => {
                    return {
                        onClick : event => this.props.onClickRow(record, rowIndex)
                    }
                }}
            />
        )
    }
};

export default ListProducts;