import React, {Component} from 'react'
import api from 'util/Api'
//import {Table} from 'antd'
import LeftList from 'components/LeftList';
import EllipsisTooltip from 'components/EllipsisTooltip';

const columns = [
    {
        title: 'Codigo da ordem',
        dataIndex: 'orderProduction',
        key: 'orderProduction',
        width: 150,
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 150
                }
            }
        },
        render: text => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    },
    {
        title: 'Produto',
        dataIndex:'product.cod',
        key: 'product',
        width: 150,
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 150
                }
            }
        },
        render: text => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    },
    {
        title: 'Cliente',
        dataIndex:'partner.name',
        key: 'partner',
        width: 150,
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 150
                }
            }
        },
        render: text => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    },

    {
        title: 'Data',
        dataIndex:'dataProd',
        key: 'dataProd',
        width: 100,
        onCell: () => {
            return {
                style: {
                    whiteSpace: 'nowrap',
                    maxWidth: 150
                }
            }
        },
        render: text => <EllipsisTooltip title={text}>{text}</EllipsisTooltip>
    },
]

const model = 'orderprod';

class ListOrdemProducao extends Component {
    constructor(props) {
        super();        
        console.log('ListUSers - Constructor')
        console.log(props)

        this.state = {
            data: []
        };
    }

    componentDidMount() {
        console.log('ComponentDidMout - CustomerList');
        this.fetchLeftList();
    } 

    fetchLeftList = () => {
        console.log('fecthLeftlist');
        api.get(`${model}?sort=id%20desc&limit=999`, {})
        .then((result) => {
            result.data.sort((a,b) => (a.id > b.id ? -1 : 1) );
            this.setState({
                data: result.data
            });
        },)
        .catch(function(error) {
            console.log('error', error)
        })
    }


    render() {
        return (
            <LeftList
                columns={columns} 
                data={this.state.data} 
                onRowClick={(record, rowIndex) => {
                    return {
                        onClick : event => this.props.onClickRow(record, rowIndex)
                    }
                } }
            />
        )
    }
};

export default ListOrdemProducao;